@import url(https://fonts.googleapis.com/css?family=Lato:400,300,300italic,400italic,700,700italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:before,
*:after {
  box-sizing: content-box;
}

body {
  max-width: 960px;
  margin: 0 auto;
  background-color: black;
  color: white;
}

.App {
  padding: 15px;
  background-color: #242424;
}

/**
*** SIMPLE GRID
*** (C) ZACH COLE 2016
**/

/* ROOT FONT STYLES */

body {
  font-family: "Lato", Helvetica, sans-serif;
  color: white;
  line-height: 1.5;
}

/* TYPOGRAPHY */

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.375rem;
}

h4 {
  font-size: 1.125rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.875rem;
}

p {
  font-size: 1.125rem;
  font-weight: 200;
  line-height: 1.8;
}

.font-light {
  font-weight: 300;
}

.font-regular {
  font-weight: 400;
}

.font-heavy {
  font-weight: 700;
}

/* POSITIONING */

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.justify {
  text-align: justify;
}

/* ==== GRID SYSTEM ==== */

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.row {
  position: relative;
  width: 100%;
}

.row [class^="col"] {
  float: left;
  margin: 0.5rem 2%;
  min-height: 0.125rem;
}

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  width: 96%;
}

.col-1-sm {
  width: 4.33%;
}

.col-2-sm {
  width: 12.66%;
}

.col-3-sm {
  width: 21%;
}

.col-4-sm {
  width: 29.33%;
}

.col-5-sm {
  width: 37.66%;
}

.col-6-sm {
  width: 46%;
}

.col-7-sm {
  width: 54.33%;
}

.col-8-sm {
  width: 62.66%;
}

.col-9-sm {
  width: 71%;
}

.col-10-sm {
  width: 79.33%;
}

.col-11-sm {
  width: 87.66%;
}

.col-12-sm {
  width: 96%;
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.hidden-sm {
  display: none;
}

@media only screen and (min-width: 33.75em) {
  /* 540px */
  .container {
    width: 80%;
  }
}

@media only screen and (min-width: 45em) {
  /* 720px */
  .col-1 {
    width: 4.33%;
  }

  .col-2 {
    width: 12.66%;
  }

  .col-3 {
    width: 21%;
  }

  .col-4 {
    width: 29.33%;
  }

  .col-5 {
    width: 37.66%;
  }

  .col-6 {
    width: 46%;
  }

  .col-7 {
    width: 54.33%;
  }

  .col-8 {
    width: 62.66%;
  }

  .col-9 {
    width: 71%;
  }

  .col-10 {
    width: 79.33%;
  }

  .col-11 {
    width: 87.66%;
  }

  .col-12 {
    width: 96%;
  }

  .hidden-sm {
    display: block;
  }
}

@media only screen and (min-width: 60em) {
  /* 960px */
  .container {
    width: 75%;
    max-width: 60rem;
  }
}

.item {
  display: inline-flex;
  align-items: center;
  padding: 5px;
}

.item__name {
  padding-left: 8px;
}

/* Normal */
.item--1 {
  color: white;
}
.item--1 .item__image {
  border: 4px solid white;
  border-radius: 2px;
}

/* Uncommon */
.item--2 {
  color: #1eff00;
}
.item--2 .item__image {
  border: 4px solid #1eff00;
  border-radius: 2px;
}

/* Rare */
.item--3 {
  color: #0070dd;
}
.item--3 .item__image {
  border: 4px solid #0070dd;
  border-radius: 4px;
}

/* Epic */
.item--4 {
  color: #a335ee;
}
.item--4 .item__image {
  border: 4px solid #a335ee;
  border-radius: 4px;
}

.item__icon {
  height: 56px;
  width: 56px;
}

.want__item {
  display: inline-flex;
  align-items: center;
  flex: 0 0 250px;
}

.want__input {
  height: 56px;
  width: 56px;
  font-size: 32px;
}

.suggestions__filter {
  padding: 15px 0;
}

.suggestions__results {
  display: flex;
  flex-wrap: wrap;
}

.suggestions__results {
  cursor: pointer;
}

.suggestions__item--selected {
  background-color: #333333;
}

.results__steps {
  border: 1px solid black;
  width: 100%;
  border-collapse: collapse;
}

.results__steps th,
.results__steps td {
  border: 1px solid black;
  padding: 5px;
}

.results__vendor {
  text-align: center;
  max-width: 256px;
}

.results__quantity {
  display: inline-block;
  width: 64px;
  height: 64px;
  font-size: 32px;
  text-align: right;
  padding: 5px;
  line-height: 64px;
}

.results__item {
  display: flex;
}

.results__other {
  font-size: 32px;
  padding-left: 74px;
}

.results__steps tr:nth-child(even) {
  background-color: #333333;
}

